    text-align: center;
    position: relative;
    border: none;
    outline: none;
    background: none;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: transparent;
    width: var(--width);
    letter-spacing: 0.5px;
    font-size: 14px;
    font-weight: 500;
    color: var(--color);
    line-height: var(--height);
  }
  .mark-done i{
      display: none;
  }
  .mark-done:before, .mark-done:after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .mark-done:before {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: var(--width);
    width: var(--before-w, var(--width));
    height: var(--height);
    height: var(--before-h, var(--height));
    border-radius: 7px;
    border-radius: var(--before-r, 7px);
    background: var(--background);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    transition: width 0.3s, height 0.3s, border-radius 0.2s;
    transition-delay: 0.2s;
  }
  .mark-done:after {
    width: 16px;
    height: 16px;
    margin: -8px 0 0 -8px;
    background: var(--icon);
    z-index: 2;
    border-radius: 50%;
    -webkit-transform: scale(0);
            transform: scale(0);
    background-color: lightblue;
  }
  .mark-done span {
    z-index: 1;
    display: block;
    position: relative;
    opacity: 1;
    opacity: var(--span-o, 1);
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transform: scale(var(--span-s, 1));
            transform: scale(var(--span-s, 1));
    transition: opacity 0.3s, -webkit-transform 0.2s;
    transition: opacity 0.3s, transform 0.2s;
    transition: opacity 0.3s, transform 0.2s, -webkit-transform 0.2s;
    transition-delay: 0.2s;
  }
    .mark-done svg {
        width: 15px;
        height: 13px;
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        stroke: var(--icon);
        stroke-width: 2px;
        stroke-linecap: round;
        stroke-linejoin: round;
        fill: none;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        stroke-dasharray: 17px;
        stroke-dashoffset: 17px;
        stroke-dashoffset: var(--svg-o, 17px);
