}



/* ----Achievements Section---- */




.achieve-sec-close{
    background-color: #fff;
    width: 23%;
    height: 60px;
    position: absolute;
    /* bottom: 34px; */
    top: 530px;
    right: 9.6vw;
    border-radius: 31px;
    display: flex;
    align-items: center;
    transition: 1s;
    cursor: pointer;
}
.achieve-sec-open{
    background-color: #fff;
    width: 23%;
    height: 300px;
    transition: 1s;
    position: absolute;
    /* bottom: 34px; */
    top: 530px;
    right: 9.6vw;
    border-radius: 31px;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.achieve-sec-close h2{
    font-size: 18px;
    font-weight: 500;
    margin-left: 1.5vw;
    position: absolute;
    top: 3px;
}
.achieve-sec-open h2{
    font-size: 18px;
    font-weight: 500;
    position: absolute;
    top: 0px;
    margin-left: 1.5vw;
}


.achieve-sec-close i{
    position: absolute;
    right: 2vw;
    top: 22px;
}
.achieve-sec-open i{
    position: absolute;
    right: 2vw;
    top: 22px;
}







/* ------------Media Query-------- */

@media screen and (max-width: 736px){
    .body-container{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .header-sec, .body-sec, .right-sec, .achieve-sec-close, .achieve-sec-open{
        width: 70%;
        margin-left: 0;
    }
    .right-sec{
        height: 400px;
        position: relative;
        margin-top: 20px;
        top: 0;
        right: inherit;
        padding-top: 25px;
        
    }
    .achieve-sec-close, .achieve-sec-open{
        position: static;
        margin-top: 20px;
        align-items: flex-start;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .achieve-sec-close h2, .achieve-sec-open h2{
       position: static;
       margin-left: 3vw;
    }
    .achieve-sec-close i, .achieve-sec-open i{
        position: static;
        margin-top: 20px;
        margin-right: 3vw;
    }

    .white-btn-sec .contact{
        font-size: 10px;
        padding: 6px 6px 4px 6px;
    }
    .white-btn-sec .edit{
        font-size: 10px;
        padding: 6px 6px 4px 6px;
    }
}


@media screen and (max-width: 588px){
    .header-sec, .body-sec, .right-sec, .achieve-sec-close, .achieve-sec-open{
        width: 80%;
    }
    .profile-pic-sec{
        width: 120px;
        height: 120px;
        top: 32px;
    }
}
@media screen and (max-width: 500px){
    .header-sec, .body-sec, .right-sec, .achieve-sec-close, .achieve-sec-open{
        width: 90%;
    }
    .white-btn-sec .contact{
        font-size: 9px;
        padding: 6px 6px 4px 6px;
    }
    .white-btn-sec .edit{
        font-size: 9px;
        padding: 6px 6px 4px 6px;
    }
}

@media screen and (max-width: 434px){
    .body-up-sec .btn{
        font-size: 9px;
        padding: 6px 10px 6px 10px;
    }
    .profile-pic-sec{
        width: 100px;
        height: 100px;
        top: 42px;
    }
}
@media screen and (max-width: 397px){
    .body-up-sec .btn{
        font-size: 8px;
        padding: 5px 8px 5px 8px;
    }
}
@media screen and (max-width: 337px){
    .body-up-sec .btn{
        font-size: 7px;
        padding: 5px 6px 5px 6px;
    }
}
