/* Theme Gradient */
.theme-gradient,
.counter-style-1 .count-number {
  background: -webkit-gradient(
      95deg,
      #059dff 15%,
      #6549d5 45%,
      #e33fa1 75%,
      #fb5343 100%
    )
    98%/200% 100%;
