      95deg,
      var(--color-primary) 15%,
      var(--color-tertiary) 45%,
      var(--color-pink) 75%,
      var(--color-secondary) 100%
    )
    98%/200% 100%;
  background: linear-gradient(
      95deg,
      #059dff 15%,
      #6549d5 45%,
      #e33fa1 75%,
      #fb5343 100%
    )
    98%/200% 100%;
