input[type="radio"] ~ label::before {
  content: " ";
  position: absolute;
  top: 6px;
  left: 0;
  width: 14px;
  height: 14px;
  background-color: #fff;
  border: 2px solid rgba(24, 152, 165, 0.2);
  border: var(--border-width) solid rgba(24, 152, 165, 0.2);
  border-radius: 2px;
  transition: all 0.3s;
}
