    180deg,
    var(var(--color-blackest)),
    rgba(19, 19, 19, 0)
  );
}

.timeline-dot {
  position: absolute;
  top: 50%;
  left: 0;
}
.timeline-dot::before,
.timeline-dot::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
